.my-card {
    min-width: 15rem;
    max-width: 18rem;
    max-height: 70vh;
    border-radius: 5px;
    box-shadow: 0rem 0rem 1.5625rem rgba(0, 0, 0, 0.1);
    padding: 0px;
    overflow: hidden;
    position: relative;
    margin: 5px auto ; 
    cursor: pointer;
    display: -ms-grid;
}

.my-card img {
    transition: all 0.15s linear;
    top: -15%;
    left: -25%;
    filter: brightness(70%);
    /* position: relative; */
    
    
    
}
.my-card:hover{
    /* filter: sepia(60%); */
    filter:saturate(180%);
    /* filter: opacity(.5); */
    /* filter: grayscale(80%); */
    /* filter: blur(1px); */
}

.my-card .profile-name {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 25px;
    font-weight: bold;
    color: #FFF;
    padding: 15px 20px;
    transition: all 0.15s linear;
}

.my-card .profile-position {
    position: absolute;
    color: rgba(255, 255, 255, 0.596);
    left: 30px;
    top: 60px;
    transition: all 0.15s linear;
}

.my-card .profile-overview {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 50%, rgba(255, 255, 0, 0));
    color: #FFF;
    padding: 50px 0px 40px 0px;
    transition: all 0.15s linear;
}

.my-card .profile-overview h3 {
    font-weight: bold;
    font-size: large;
    padding-left: 25px;
}
 
.my-card .profile-overview p {
    color: rgba(255, 255, 255, 0.7);
    padding-left: 25px;
    padding-right: 25px;
}
 
.my-card:hover .profile-name {
    padding-left: 25px;
}
 
.my-card:hover .profile-position {
    left: 40px;
}
 
.my-card:hover .profile-overview {
    padding-bottom: 25px;
}
 
.my-card img:hover {
    transform: scale(1.01);
}